<template>
	<div class="usinghelpo-container">
		<div class="usinghelpo-header">
			<div class="usinghelpo-zhong">
				<div class="usinghelpo-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="usinghelpo-ti">使用帮助</div>
				<div></div>
			</div>
		</div>
		<div class="usinghelpo-content">
			<div class="usinghelpo-bg"  v-for="item in datalist">
				<img :src="baseUrl+item.help_img">
			</div>
			<!--<div class="usinghelpo-neirong">{{data.help_content}}</div>-->
		</div>
	</div>
</template>

<script>
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				data: {},
				datalist: []
			}
		},
		methods: {
			back() {
				window.history.back();
			},
			go_usinghelpt() {
				this.$router.push('/usinghelpt')
			},
			info_help() {
				var that = this;
				that.$http.post('/help/info_help', {}).then((res) => {
					if(res.meta.code == 200) {
						that.data = res.data;
					}
				})
			},
			helpquery() {
				var that = this;
				that.$http.post('/s/help/query', {}).then((res) => {
//					console.log(res)
					if(res.code == "0") {
						that.datalist = res.data;
//						console.log(that.datalist)
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			this.helpquery();
			this.info_help();

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.usinghelpo-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.usinghelpo-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.usinghelpo-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.usinghelpo-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.usinghelpo-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.usinghelpo-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
		/*background-image: url('../assets/bg_sybz1.png');
		background-repeat: no-repeat;
		background-size: 100% 100%;
		height: 100%;
		width: 100%;*/
	}
	.usinghelpo-bg img{
		width: 100%;
		height: auto;
	}
	.usinghelpo-neirong{
		font-size: 1.5rem;
		padding-left: 1.5rem;
		padding-right: 1.5rem;
		padding-bottom: 1.5rem;
	}
</style>


